<template>
  <div class="productcate">
    <bread-crumb></bread-crumb>
      <transition name="slide-fade">
    <el-card v-if="show">
      <!-- & -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-form>
            <el-input
              v-model="queryInfo.query"
              placeholder=""
              clearable
            >
              <template #append>
                <el-button
                  icon="el-icon-search"
                  @click="getProCate()"
                ></el-button>
              </template>
            </el-input>
          </el-form>
        </el-col>
        <el-col :span="1">
          <el-button
            type="primary"
            icon="el-icon-circle-plus-outline"
            @click="showAddDialog0()"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        :data="proCateList"
        style="width: 100%; margin-bottom: 20px"
        row-key="id"
        border
        default-expand-all
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
        <el-table-column prop="title" label="" sortable>
        </el-table-column>
        <el-table-column prop="id" label="id" sortable> </el-table-column>
        <el-table-column prop="level" label="" sortable> 
           <template #default="scope">
          <el-tag type="" v-if="scope.row.level==1"></el-tag>
            <el-tag type="success" v-else-if="scope.row.level==2"></el-tag>
            <el-tag type="warning" v-else-if="scope.row.level==3"></el-tag>
           </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <el-button
              type="primary"
              icon="el-icon-circle-plus-outline"
              size="mini"
              @click="showAddDialog(scope.row.id, scope.row.level)"
            ></el-button>
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeById(scope.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
      </transition>
    <!--  -->
    <el-dialog
      title=""
      v-model="dialogVisible0"
      width="30%"
      :before-close="handleClose"
      @close="addDiglogClose0()"
    >
      <el-form
        :model="addForm0"
        :rules="addFormRules0"
        ref="addFormRef0"
        label-width="90px"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="addForm0.title" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible0 = false"> </el-button>
          <el-button type="primary" @click="addProCate0()"> </el-button>
        </span>
      </template>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      v-model="dialogVisible"
      width="30%"
      :before-close="handleClose"
      @close="addDiglogClose()"
    >
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="90px"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="addForm.title" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="addProCate()"> </el-button>
        </span>
      </template>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      v-model="editDialogVisible"
      width="30%"
      :before-close="handleClose"
      @close="editDiglogClose()"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="90px"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="editForm.title" autocomplete="off"></el-input>
        </el-form-item>
        <!--<el-form-item label="" prop="title">
        <el-input v-model="editForm.pid" autocomplete="off"></el-input>
      </el-form-item>-->
       <el-form-item label="" prop="title">
      <el-cascader style="width:100%"
    :options="editOption"
    :props="optionProps"
    @change="handleChange"
    placeholder=""
    clearable></el-cascader>
       </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editProCate()"> </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import BreadCrumb from "../components/BreadCrumb";
export default {
  name: "ProductCate",
  components: {
    BreadCrumb,
  },
  data() {
    return {
      show:false,
      //
      queryInfo: {
        //
        query: "",
        //
        pagenum: 1,
        //
        pagesize: 10,
      },
      total: 0, //
      proCateList: [],
      dialogVisible0: false,
      dialogVisible: false,
      editDialogVisible: false,
      //
      addForm0: {
        title: "",
        pid: 0,
        level: 1,
      },
      addFormRules0: {
        title: [{ required: true, message: "", trigger: "blur" }],
      },
      addForm: {
        title: "",
        pid: 0,
        level: 1,
      },
      addFormRules: {
        title: [{ required: true, message: "", trigger: "blur" }],
      },
      editForm: {
        title: "",
        pid: 0,
        level: 1,
      },
      editFormRules: {
        title: [{ required: true, message: "", trigger: "blur" }],
      },
      checkedOptions:{pid:-1},
      editOption: [],
      optionProps: {
      checkStrictly: true,
		  value: 'id',
		  label: 'title',
      children:'children',
	}
    };
  },
  created() {
    this.getProCate();
  },
  mounted(){
    this.fadeIn();
  },
  methods: {
    fadeIn(){
      this.show=!this.show;
    },
    getProCate() {
      let _this = this;
      this.axios
        .post("ProductCate/getList", this.queryInfo)
        .then(function (res) {
          if (res.data.status == 1) {
            _this.proCateList = res.data.result;
            _this.total = res.data.extra;
            console.log(res);
          } else {
            _this.$message.error(res.data.message);
          }
        });
    },
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.getProCate();
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.queryInfo.pagenum = val;
      this.getProCate();
    },
    //
    showAddDialog0() {
      this.dialogVisible0 = true;
    },
    //
    addDiglogClose0() {
      this.$refs.addFormRef0.resetFields();
    },
    //
    addDiglogClose() {
      this.$refs.addFormRef.resetFields();
    },
    //
    showEditDialog(id) {
      let _this = this;
      this.axios
        .post("ProductCate/getParentList",{id: id})
        .then(function (res) {
          if (res.data.status == 1) {
            _this.editOption = res.data.result; 
          } else {
            _this.$message.error(res.data.message);
          }
        });
      this.axios
        .post("productcate/getProCate", { id: id })
        .then(function (res) {
          if (res.data.status == 1) {
            _this.editForm['pid']=res.data.result['pid'];
            _this.editForm['level']=res.data.result['level'];
            _this.editDialogVisible = true;
            _this.editForm = res.data.result;
          } else {
            _this.$message.error(res.data.message);
          }
        });
    },
    //
    editDiglogClose() {
      this.$refs.editFormRef.resetFields();
    },
    //
    addProCate0() {
      let _this = this;
      _this.$refs.addFormRef0.validate((valid) => {
        if (valid) {
          _this.axios
            .post("ProductCate/add0", _this.addForm0)
            .then(function (res) {
              //
              if (res.data.status == 1) {
                _this.$message.success({
                  message: res.data.message,
                  type: "success",
                });
                _this.dialogVisible0 = false;
                console.log(res);
                _this.getProCate();
              } else {
                _this.$message.error(res.data.message);
                console.log(res);
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //
    showAddDialog(id, level) {
      this.dialogVisible = true;
      this.addForm.pid = id;
      this.addForm.level = level + 1;
    },

    //
    addProCate() {
      let _this = this;
      _this.$refs.addFormRef.validate((valid) => {
        if (valid) {
          _this.axios
            .post("ProductCate/add", _this.addForm)
            .then(function (res) {
              //
              if (res.data.status == 1) {
                _this.$message.success({
                  message: res.data.message,
                  type: "success",
                });
                _this.dialogVisible = false;
                _this.getProCate();
              } else {
                _this.$message.error(res.data.message);
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //
      editProCate(){
      let _this = this;
      _this.$refs.editFormRef.validate((valid) => {
         if (valid) {
           //console.log(_this.editForm);
           //return false;
          _this.axios.put('productCate/edit/',_this.editForm)
          .then(function (res) {
            if (res.data.status == 1) {
              _this.$message.success({
                message: res.data.message,
                type: "success",
              });
              _this.editDialogVisible=false;
              _this.getProCate();
            } else {
              _this.$message.error(res.data.message);
            }
          });
        } else {
          return false;
        }
    });
  },

    //
      removeById(id){
      let _this = this;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
           _this.axios.put('ProductCate/remove/',{id:id})
          .then(function (res) {
            if (res.data.status == 1) {
              _this.$message.success({
                message: res.data.message,
                type: "success",
              });
              _this.getProCate();
            } else {
              _this.$message.error(res.data.message);
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    





  },

  handleChange(event){
            this.editForm['pid'] = event[event.length-1];
            this.editForm['level'] = event.length+1; 
  }


  },
};
</script>

<style scoped lang="scss">
.el-table--border {
  margin: 25px 0;
}



</style>